@import url('https://fonts.googleapis.com/css2?family=Noto+Sans+Wancho&display=swap');

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: 'Noto Sans Wancho', sans-serif;
  background-color: rgb(179, 210, 235);
}

.App {
  margin: auto;
  width: 80%;
}

/* navbar */
.navbar {
  display: flex;
  min-height: 3em;
  align-items: stretch;
  border-bottom: 2px solid rgb(0, 0, 0);
  margin-bottom: 1em;

}

.navbar a {
  text-decoration: none;
  color: black;
}

.logo-container {
  height: 2em;
}

.nav-logo {
  font-size: 1.4em;
  font-weight: bold;
  display: flex;
  align-items: center;
}

.nav-links {
  display: flex;
  align-items: center;
  margin-left: auto;
}

.nav-link {
  padding: .6em;
  transition: .1s;
  margin: 0 .2em;
}

.nav-link:hover {
  background-color: rgb(245, 239, 239);
  border-radius: 3px;
}

.active {
  font-weight: bold;
  border-radius: 3px;
}

/* body */
.info-container,
.technologies {
  background-color: rgb(222, 235, 247);
  border: 1px solid rgba(194, 186, 186, 0.493);
  box-shadow: 0 0 5px rgb(141, 167, 173);
  padding: 2em;
  border-radius: 4px;
  display: flex;
}

.info-container {
  margin-bottom: 2em;
}

.my-img {
  width: 18em;
  height: 18em;
  margin-right: 1em;
  border-radius: 5px;
}

.intro-text-container {
  width: 100%;
}

.technologies {
  padding: 1em 2em 2em 2em;
  flex-direction: column;
  text-align: center;
  margin-bottom: 1em;
}

.technologies h1 {
  margin: auto;
  border-bottom: 2px solid rgb(168, 156, 156);
  padding-bottom: .3em;
}

.tech-list p {
  cursor: pointer;
  font-size: 1.3em;
  margin: .5em;
}



/* projects */
.projects-container {
  text-align: center;
  border: 1px solid rgba(194, 186, 186, 0.493);
  box-shadow: 0 0 5px rgb(202, 199, 199);
  border-radius: 4px;
}

.projects-list {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}

.project {
  background-color: rgb(230, 225, 219);
  border-radius: 5px;
  margin: 1em;
  height: 20em;
  width: 20em;
}